.home {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.home_header {
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
    align-items: center;
}

.home_headerLeft {
    display: flex;
    align-items: center;
}

.home_header p {
    margin-right: 20px;
    font-size: 15px;
}

.home_headerRight {
    display: flex;
    align-items: center;
}

.home_headerRight p {
    margin-left: 20px;
    font-size: 15px;
}

.home_headerRight>svg {
    margin-left: 20px;
}

.home_headerRight .MuiAvatar-root {
    margin-left: 20px;
}

.home_body {
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-top: 10%;
}

.home_body>img {
    object-fit: contain;
    height: 100px;
}